.tabs{
    width: 100%;
    &.gender-tabs{
        display: flex;
        a{
            flex-grow: 1;
            flex-basis: 0;
            text-align: center;
            padding: 15px 20px;
            color: #767676;
            background-color: #f6f6f6;
            border: 1px solid #cdcdcd;
            border-left: 0;
            &:last-child{
                border-right: 0;
            }
            span{
                padding-bottom: 4px;
            }
            &:hover{
                span{
                    border-bottom: solid 2px #5fb6ba;

                }
            }
            
            
            &.active{
                background-color: white;
                color: black;
                border-bottom: 0;

                span{
                    border-bottom: solid 2px #5fb6ba;
                }
            }
        }
    }
}