@import '../../default/breakpoints';

.description-container {
    width: 55%;

    &.description-type-1 {
        margin: 20px auto 60px;
        text-align: center;
        width: 100%;
        padding: 0 4%;
        @include bp(sm){
            width: 55%;
            padding: 0;

        }

        h3 {
            text-align: center;
            margin-bottom: 20px;
        }

        p {
            text-align: center;
            margin-bottom: 20px;
        }

        .Body-1-Caption {
            text-align: center;
        }
    }

    &.description-type-2 {
        margin: 20px auto 30px;

        h3 {
            margin-bottom: 15px;
            text-align: center;
        }

        p {
            margin-bottom: 25px;
        }
    }

    &.description-type-3 {
        margin: 35px auto;

        h3 {
            margin-bottom: 25px;
            text-align: center;
        }

        p {
            font-size: 22px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.36;
            letter-spacing: 1px;
            text-align: center;
            color: #000000;
            margin-bottom: 20px;

        }
    }

    &.description-type-4 {
        margin: 10px auto;
        text-align: center;
        width: 92%;
        padding: 10px 4%;
        img {
            margin-bottom: 20px;
        }
    
        @include bp(sm) {
            text-align: left;
            padding: 10px 40px;
            img {
                display: block;
            }
            display: grid;
            grid-template-columns: 40% 1fr;
            grid-column-gap: 30px;
            width: calc(100% - 80px);
    
        }
    
        @include bp(mid) {
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 70px;
            width: calc(100% - 80px);
        }
      
        div{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
        }

        h3 {
            margin-bottom: 25px;
            display: block;
            width: 100%;
        }

        p {
            margin-bottom: 20px;
            width: 100%;
        }
    }
}