.info-cards-container {
    display: grid;
    grid-template-columns: 1fr ;
    grid-row-gap: 30px;
    grid-column-gap: 40px;
    width: 100%;
    padding: 0 4%;

    .info-card {
        padding: 24px;
        box-shadow: 0 1px 5px 1px rgba(0, 0, 0, 0.14);
    }

    .info-card-Cont {
        display: block;
        margin-top: 20px;
    }
    p{
        text-align: left;
    }
    .Header-4, .subtitle{
        text-align: center;
    }
    img{
        margin-bottom: 25px;
    }

    @include bp(xs) {
        img{
            margin-bottom: 0px;
        }

        .Header-4, .subtitle, p{
            text-align: left;
        }
    
        width: 80%;
        margin: 0 auto;
        .info-card {
            margin-bottom: 20px;
        }

        .info-card-Cont {
            display: grid;
            gap: 20px;
            grid-template-columns: 40% 1fr;
            margin-top: 20px;

            img {
                width: 100%;
                height: auto;
                margin: 0 auto 20px;
            }

            p {
                // width: 50%;
                margin: 0 auto 20px;
                text-align: left;
            }
        }
    }

    @include bp(mid){
        width: calc(100% - 80px);
        padding: 0 40px;
        grid-template-columns: 1fr 1fr;
        
    }
}