@import './default/breakpoints';
$uniqloReg: "Uniqlo Pro Regular",
"uniqloRegregular",
"Helvetica Neue",
Helvetica,
Arial;

$uniqloBold: "Uniqlo Pro Bold",
"$uniqloRegbold",
"Helvetica Neue",
Helvetica,
Arial;

$heleveticaNeue: "Helvetica Neue",
Helvetica,
Arial;

//Tokens
:root {
  --helvetica: #{$heleveticaNeue};
  --uniqloProReg: #{$uniqloReg};
  --uniqloProBold: #{$uniqloBold};
}



// Plugins

.App {

  // @import './default/reset';
  li {
    list-style-type: none;
  }
}

.modal-dialog {
  max-width: 650px;
  width: 100%;
}

// Default
.feature-page {
  @import './default/colors';
  @import './default/typography';
  height: 100%;
  font-family: 'Helvetica Neue', Helvatica, Arial;
  font-size: 16px;
  background-color: #fff;


  li {
    list-style-type: none;
  }

  a {
    text-decoration: none;
    color: #000;
  }


  a {
    text-decoration: none;
    color: #000;

    &:hover {
      text-decoration: none;
    }
  }


  img {
    display: block;
    width: 100%;
    height: auto;
  }
}


// Components 
@import './components/atoms/buttons';
@import './components/atoms/Hero';
@import './components/atoms/MenuItem';
@import './components/atoms/Video';
@import './components/atoms/AboutDescription';
@import './components/atoms/DividerHeading';
@import './components/atoms/Function';
@import './components/Quotes';
@import './components/atoms/InfoCard';
@import './components/atoms/Tabs';





@import './components/molecules/products';
@import './components/molecules/MenuContainer';

.feature-page {

  .showMB {
    display: block;

    @include bp(sm) {
      display: none;

    }
  }

  .showTB {
    display: none;

    @include bp(sm) {
      display: block;
    }

    @include bp(mid) {
      display: none;
    }
  }


  .showPC {
    display: none;

    @include bp(mid) {
      display: block;
    }
  }

  .pc {
    display: none;

    @include bp(sm) {
      display: block;
    }
  }

  .mb {
    display: block;

    @include bp(sm) {
      display: none;
    }
  }
}