.menu-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin: 0 auto;
    margin-bottom: 30px;

    &.menu-container-1{
        margin-top: 35px;
        .menu-item{
            max-width: calc(40% - 20px);

            @include bp(sm){
                max-width: calc(25% - 40px);

            }
            @include bp(mid){
                max-width: calc(25% - 80px);

            }
            margin-bottom: 25px;
            p{
                margin: 15px 0 5px;
            }
            .menu-caption{
                margin:0;
            }
        }
    }

    &.menu-container-2{
        margin-top: 35px;
        .menu-item{
            max-width: calc(40% - 20px);

            @include bp(sm){
                max-width: calc(25% - 40px);

            }
            @include bp(mid){
                max-width: calc(25% - 80px);

            }
            p{
                padding: 0 5px;
            }
            margin-bottom: 25px;
            position: relative;
            div{
                position: absolute;
                top:0;
                left: 0;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: rgba(0,0,0, .1);
                color: #fff;
            }
        }
    }
    &.menu-container-3{
        width: 85%;
        padding: 20px 10px;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-evenly;
        margin-bottom: 0;
        .menu-item{
            margin-bottom: 25px;
            position: relative;
            margin: 0 20px;
            padding: 10px 0 5px;
            transition: .35s;
            &.menu-item-active, &:hover{
                border-bottom: 1px solid #f00;
            }
        }
    }
    &.menu-container-4{
        width: 100%;
        padding: 20px 10px;
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-start;
        margin-bottom: 0;
        position: relative;
        .menu-sticky {
            position: fixed;
            width: 100%;
            right: 0;
            left: 0;
            top: 0;
            z-index: 1;
            background-color: #fafafa;
        }
    
        &.menu-container {
            background-color: #fafafa;
            width: calc(100% - 20px);
            transition: .3s;
            max-width: 1280px;
            margin: 0 auto;
    
            overflow-x: scroll;
    
            @include bp(sm) {
                overflow-x: initial;
            }
    
            button {
                top: 15px;
                border: 2px solid transparent;
    
                &:hover {
                    border: 2px solid black;
                }
            }
    
            @include bp(sm) {
                overflow-x: none;
            }
    
            .menu-item {
                margin: 0 30px 0 0px;
                padding: 5px 0;
    
                a {
                    p {
                        margin-top: 0;
                        white-space: nowrap;
                        margin-bottom: 0px;
    
    
                    }
                }
            }
        }
    
        .menu-item{
            margin-bottom: 25px;
            position: relative;
            margin: 0 20px;
            padding: 10px 0 5px;
            transition: .35s;
            border-bottom: 1px solid transparent;

            &.menu-item-active, &:hover{
                border-bottom: 1px solid #f00;
            }
        }
        button{
            position: absolute;
            right: 30px;
            background-color: #363636;
            padding: 8px 20px;
            font-family: var(--helvetica);
            font-size: 16px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.44;
            letter-spacing: normal;
            text-align: center;
            color: #ffffff;
            border:0;
            outline: 0;
        }
    }
}

