@import '../default/breakpoints';
.quotes-container {
    @include bp(sm) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 20px;
    }


    .quote-item {
        position: relative;
        padding: 30px 40px;

        @include bp(sm) {

            padding: 40px 50px;
        }

        .quote-img {
            position: absolute;

            &.quote-left {
                top: 10px;
                left: 10px;

                @include bp(sm) {
                    top: 0px;

                }
            }

            &.quote-right {
                bottom: 10px;
                right: 10px;

                @include bp(sm) {
                    bottom: 0px;

                }

            }

            img {
                width: 20px;

                @include bp(sm) {

                    width: 30px;
                }
            }
        }
    }
}